body {
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #292b2c;
  background-color: #fafafa;
}

.container {
  padding: 15px;
}

.commit-file {
  background-color: #ffffff;
  -webkit-box-shadow: 0 0 5px lightgray;
          box-shadow: 0 0 5px lightgray;
  margin-top: 5ch;
}

.file-name {
  background-color: #f5f5f5;
  padding: 1ch;
}

.file-name span {
  opacity: 0.54;
}

.file-lines {
  font-family: 'Inconsolata';
  font-size: 14px;
  padding: 2ch;
  line-height: 2.75ex;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}

.file-side {
  display: -ms-flexbox;
  display: flex;
}

.file-lines .gutter {
  width: 5ch;
  height: 100%;
  padding-right: 1ch;
  text-align: right;
  white-space: pre;
  opacity: 0.3;
  z-index: 1;
}

.file-lines .hunks {
  white-space: pre;
  width: 100ch;
  color: rgb(41, 43, 44);
}

.file-lines .alignment {
  width: 5ch;
}

.hunk, .lines {
  margin-left: -6ch;
  padding-left: 6ch;
}

.left .hunk-removed { border-right: 1px solid #c62828; }
.left .hunk-added { border-right: 1px solid #00695c; }
.left .hunk-both { border-right: 1px solid #424242; }
.right .hunk-removed { border-left: 1px solid #c62828; }
.right .hunk-added { border-left: 1px solid #00695c; }
.right .hunk-both { border-left: 1px solid #424242; }

.lines-header {
  background-color: #e3f2fd;
  opacity: 0.5;
}

.lines-removed { background-color: #ffebee; }
.lines-added { background-color: #e0f2f1; }

.lines-blank {
  margin-top: -1px;
  margin-bottom: -1px;
}
.left .lines-blank { border: 1px solid #e0f2f1; }
.right .lines-blank { border: 1px solid #ffebee; }

.comment-avatars {
  width: 2.75ex;
  position: relative;
}

.comment-avatar {
  width: 2.75ex;
  height: 17px;
  position: absolute;
  border-radius: 50%;
  z-index: 2;
}

.horizon {
  width: 100%;
  height: 1px;
  background-color: #E0E0E0;
  position: fixed;
  top: 50%;
  z-index: 1;
}



